@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap");

.App {
  text-align: center;
}

.body {
  font-family: "Roboto Slab", serif;
  text-align: center;
}

.inputText {
  padding: 5px;
}

.addBtn {
  padding: 5px;
  color: white;
  background-color: black;
  cursor: pointer;
  margin-left: 3px;
  border-radius: 3px;
}

.listItems {
  margin-left: 150px;
  margin-right: 150px;
}

.objectsList {
  display: flex;
  justify-content: space-between;
  border: 2px solid black;
  width: 1200px;
  margin: 10px;
}

.buttonContainer {
  display: flex;
  align-items: center;
  margin-right: 12.5px;
}

.delBtn {
  color: white;
  background-color: red;
  height: min-content;
  border-radius: 5px;
  cursor: pointer;
}
.checkbox {
  position: relative;
  transform: scale(1.8);
  left: 12.5px;
  cursor: pointer;
}

.displayItems {
  font-size: 1.25rem;
  margin: 10px;
}

.irritated {
  display: flex;
  gap: 10px;
}

@media only screen and (max-width: 600px) {
  .objectsList {
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    width: 350px;
  }
  .listItems {
    margin-left: 2px;
    margin-right: 2px;
  }
  .displayItems {
    font-size: 0.9rem;
  }
  .checkbox {
    transform: scale(1.2);
  }
  .note {
    font-size: 0.8rem;
    font-weight: 300;
  }
}
